import * as S from "../styles";
import {useTranslation} from "react-i18next";

function Status({status}) {
  const { t } = useTranslation();
  return(
      <S.Table width="49%">
        <caption>{t(`captions.status`)}</caption>
        <thead>
          <tr>
            <th>
              <S.Icon><img src={`${process.env.PUBLIC_URL}/images/icons/hp.webp`} alt="" title="" /></S.Icon>
            </th>
            <th>
              <S.Icon><img src={`${process.env.PUBLIC_URL}/images/icons/speed.webp`} alt="" title="" /></S.Icon>
            </th>
            <th>
              <S.Icon><img src={`${process.env.PUBLIC_URL}/images/icons/defense.webp`} alt=" " title=" " /></S.Icon>
            </th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{status.hp}</td>
            <td>{status.speed}</td>
            <td>{status.defense}</td>
          </tr>
        </tbody>
      </S.Table>
  )
}

export default Status;
